import { FastField, Form, Formik } from "formik";
import { useDispatch } from "react-redux";
import InputField from "./custom/InputField";
import options from "./custom/option";
import SelectField from "./custom/Select";
import { HandleClickBtnClose } from "./toggleModalBox";
import * as yup from "yup";
import { patchUpdateStaff } from "../actions";



function FormUpdateStaff(props) {
  const dispatch = useDispatch();
  const staff = props.staff;
  

  const HandleSubmitForm = (values) => {
    if (values.name) {
      values.id = staff.id
      dispatch(patchUpdateStaff(values))
     
      console.log(values);
    }
  };


  const initialValues = {
    name: staff.name,
    doB: '',
    startDate: '',
    departmentId: '',
    annualLeave:'',
    overTime: '',
    salaryScale:'',
    image:
      "https://png.pngtree.com/png-clipart/20200225/original/pngtree-businessman-avatar-icon-vector-download-vector-user-icon-avatar-silhouette-social-png-image_5257566.jpg",
  };

  const validationSchema = yup.object().shape({
    name: yup.string().required("Hy Nhp tn nhn vin"),

    doB: yup.date().required("Ngy sinh khng hp l"),

    startDate: yup.date().required("Ngy vo cng ty khng hp l"),

    departmentId: yup.string().required("Thng tin ny l bt buc"),

    annualLeave: yup
      .number("Vui lng nhp s")
      .required("Thng tin ny l bt buc"),

    overTime: yup
      .number("Vui lng nhp s")
      .required("Thng tin ny l bt buc"),

    salaryScale: yup
      .number("Vui lng nhp s")
      .required("Thng tin ny l bt buc"),
  });

  return (
    <div className="box_modal">
      <div className="box_modal__title" >
        <h4>Cp Nht</h4>
      </div>
      <div className="btn-close-form" onClick={HandleClickBtnClose}>
        <i className="fas fa-times"></i>
      </div>

      <div className="form_staff">
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={(values) => HandleSubmitForm(values)}
        >
          {(formikProps) => {
            const { values, errors, touched } = formikProps;
            console.log({ values, errors, touched });
            return (
              <Form>
                <FastField
                  name="name"
                  component={InputField}
                  label="H v tn"
                  placeholder="Nhp h v tn"
                  type="text"
                />
                <FastField
                  name="doB"
                  component={InputField}
                  label="Ngy sinh"
                  placeholder="dd/mm/yyyy"
                  type="date"
                />
                <FastField
                  name="startDate"
                  component={InputField}
                  label="Ngy vo cng ty"
                  placeholder="dd/mm/yyyy"
                  type="date"
                />
                <FastField
                  name="departmentId"
                  component={SelectField}
                  label="Phng ban"
                  placeholder="Chn phng ban"
                  options={options}
                />
                <FastField
                  name="annualLeave"
                  component={InputField}
                  label="Ngy ngh cn li"
                  type="text"
                />
                <FastField
                  name="overTime"
                  component={InputField}
                  label="Ngy lm thm"
                  type="text"
                />
                <FastField
                  name="salaryScale"
                  component={InputField}
                  label="H s lng"
                  type="text"
                />

                <button
                  className="btn-submit"
                  onClick={(values) => HandleSubmitForm(values)}
                  type="submit"
                >
                    Cp Nht
                </button>
              </Form>
            );
          }}
        </Formik>
      </div>
    </div>
  );
}

export default FormUpdateStaff;
